import React, { useRef, useCallback, useEffect } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ChartConfiguration
} from 'chart.js';
import annotationPlugin from 'chartjs-plugin-annotation';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  annotationPlugin
);

interface DualAxisChartData {
  time: number;
  frequency: number;  // Hz
  convertedValue: number; // Semitone/Q-tone
}

export const useDualAxisChart = (
  canvasRef: React.RefObject<HTMLCanvasElement>,
  API_BASE: string,
  referenceFreq: number = 200
) => {
  const chartRef = useRef<ChartJS | null>(null);
  const chartDataRef = useRef<DualAxisChartData[]>([]);
  const [yAxisUnit, setYAxisUnitInternal] = React.useState<'semitone' | 'qtone'>('semitone');

  // 🎯 외부에서 Y축 단위를 설정하는 함수
  const setYAxisUnit = useCallback((newUnit: 'semitone' | 'qtone') => {
    setYAxisUnitInternal(newUnit);
  }, []);

  // 🎯 주파수 → 세미톤/큐톤 변환 함수 (usePitchChart와 동일한 공식 사용)
  const convertFrequencyToUnit = useCallback((frequency: number): number => {
    const baseFreq = referenceFreq; // 🎯 매개변수로 받은 기준 주파수 사용
    if (yAxisUnit === 'semitone') {
      // 세미톤: 12 * log2(f/baseFreq) - 개인화된 기준 주파수 적용
      return 12 * Math.log2(frequency / baseFreq);
    } else {
      // 큐톤: 24 * log2(f/baseFreq) - 개인화된 기준 주파수 적용 (Quarter-tone = 세미톤 × 2)
      return 24 * Math.log2(frequency / baseFreq);
    }
  }, [yAxisUnit, referenceFreq]);

  // 🎯 차트 초기화
  const initChart = useCallback(() => {
    if (!canvasRef.current) return;

    if (chartRef.current) {
      chartRef.current.destroy();
    }

    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;

    const config: ChartConfiguration = {
      type: 'line',
      data: {
        labels: [],
        datasets: [
          {
            label: '주파수 (Hz)',
            data: [],
            borderColor: 'rgb(255, 99, 132)',
            backgroundColor: 'rgba(255, 99, 132, 0.2)',
            tension: 0.1,
            pointRadius: 2,
            pointHoverRadius: 4,
            yAxisID: 'y-frequency'
          },
          {
            label: '세미톤/큐톤',
            data: [],
            borderColor: 'rgb(54, 162, 235)',
            backgroundColor: 'rgba(54, 162, 235, 0.2)',
            tension: 0.1,
            pointRadius: 2,
            pointHoverRadius: 4,
            yAxisID: 'y-converted'
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        interaction: {
          intersect: false,
          mode: 'index'
        },
        plugins: {
          title: {
            display: true,
            text: '🎯 변환 공식 검증 차트 - 실시간 피치 값 비교',
            font: {
              size: 16,
              weight: 'bold'
            }
          },
          tooltip: {
            callbacks: {
              title: (tooltipItems) => {
                const dataIndex = tooltipItems[0].dataIndex;
                const timeValue = chartDataRef.current[dataIndex]?.time || 0;
                return `시간: ${timeValue.toFixed(2)}초`;
              },
              label: (tooltipItem) => {
                const dataIndex = tooltipItem.dataIndex;
                const data = chartDataRef.current[dataIndex];
                if (!data) return '';

                if (tooltipItem.datasetIndex === 0) {
                  return `주파수: ${data.frequency.toFixed(1)} Hz`;
                } else {
                  const unit = yAxisUnit === 'semitone' ? 'st' : 'Q';
                  return `${yAxisUnit === 'semitone' ? '세미톤' : '큐톤'}: ${data.convertedValue.toFixed(1)} ${unit}`;
                }
              }
            }
          },
          legend: {
            display: true,
            position: 'top'
          },
          annotation: {
            annotations: {}
          }
        },
        scales: {
          x: {
            type: 'linear',
            position: 'bottom',
            title: {
              display: true,
              text: '시간 (초)'
            }
            // min, max 제거 - 데이터에 맞게 동적 설정 (0.2초 마진 포함)
          },
          'y-frequency': {
            type: 'linear',
            position: 'left',
            title: {
              display: true,
              text: '주파수 (Hz)',
              color: 'rgb(255, 99, 132)'
            },
            ticks: {
              color: 'rgb(255, 99, 132)'
            },
            grid: {
              drawOnChartArea: false
            }
            // min, max 제거 - 데이터에 맞게 동적 설정
          },
          'y-converted': {
            type: 'linear',
            position: 'right',
            title: {
              display: true,
              text: '세미톤/큐톤',
              color: 'rgb(54, 162, 235)'
            },
            ticks: {
              color: 'rgb(54, 162, 235)'
            },
            grid: {
              drawOnChartArea: true
            }
            // min, max 제거 - 데이터에 맞게 동적 설정
          }
        }
      }
    };

    chartRef.current = new ChartJS(ctx, config);
    console.log('🎯 듀얼 Y축 차트 초기화 완료');
  }, [canvasRef]);

  // 🎯 데이터 추가 함수
  const addDualAxisData = useCallback((frequency: number, timestamp: number, type: 'reference' | 'live' = 'reference') => {
    if (!chartRef.current) return;

    const convertedValue = convertFrequencyToUnit(frequency);
    const chartData: DualAxisChartData = {
      time: timestamp,
      frequency,
      convertedValue
    };

    chartDataRef.current.push(chartData);

    // 차트 데이터 업데이트
    chartRef.current.data.labels!.push(timestamp.toFixed(2));
    chartRef.current.data.datasets[0].data.push({ x: timestamp, y: frequency });
    chartRef.current.data.datasets[1].data.push({ x: timestamp, y: convertedValue });

    // 🎯 Y축 범위 조정은 충분한 데이터가 쌓인 후에만 수행
    if (chartDataRef.current.length >= 5) {
      updateYAxisRanges();
    }

    // 색상 구분 (참조 vs 실시간) - type assertion으로 해결
    if (type === 'live') {
      (chartRef.current.data.datasets[0] as any).pointBackgroundColor = (chartRef.current.data.datasets[0] as any).pointBackgroundColor || [];
      (chartRef.current.data.datasets[1] as any).pointBackgroundColor = (chartRef.current.data.datasets[1] as any).pointBackgroundColor || [];
      
      ((chartRef.current.data.datasets[0] as any).pointBackgroundColor as string[]).push('rgb(76, 175, 80)'); // 녹색
      ((chartRef.current.data.datasets[1] as any).pointBackgroundColor as string[]).push('rgb(76, 175, 80)'); // 녹색
    }

    chartRef.current.update('none');
    console.log(`📊 듀얼축 데이터 추가: ${frequency.toFixed(1)}Hz → ${convertedValue.toFixed(1)}`);
  }, [convertFrequencyToUnit]);

  // 🎯 Y축 및 X축 범위 자동 조정 함수
  const updateYAxisRanges = useCallback(() => {
    if (!chartRef.current || chartDataRef.current.length === 0) return;

    const scales = chartRef.current.options.scales;
    if (!scales) return;

    // X축 시간 범위 조정 (0.2초 마진 추가)
    const xScale = scales.x as any;
    if (xScale) {
      const allTimes = chartDataRef.current.map(d => d.time);
      if (allTimes.length > 0) {
        const minTime = Math.min(...allTimes);
        const maxTime = Math.max(...allTimes);
        const timeMargin = 0.2; // 0.2초 마진
        
        xScale.min = minTime - timeMargin; // 0으로 제한하지 않고 음수 허용
        xScale.max = maxTime + timeMargin;
        console.log(`📊 듀얼차트 X축 범위: ${xScale.min.toFixed(1)}s ~ ${xScale.max.toFixed(1)}s (마진: ${timeMargin}s)`);
      }
    }

    // 주파수 축(왼쪽) 범위 조정
    const frequencyScale = scales['y-frequency'] as any;
    if (frequencyScale) {
      const allFrequencies = chartDataRef.current.map(d => d.frequency).filter(f => f > 0);
      if (allFrequencies.length > 0) {
        const minFreq = Math.min(...allFrequencies);
        const maxFreq = Math.max(...allFrequencies);
        const freqMargin = Math.max(Math.abs(maxFreq - minFreq) * 0.1, 20); // 최소 20Hz 마진
        
        frequencyScale.min = Math.max(50, Math.floor(minFreq - freqMargin)); // 최소 50Hz
        frequencyScale.max = Math.ceil(maxFreq + freqMargin);
        console.log(`📊 듀얼차트 주파수축 범위: ${frequencyScale.min}Hz ~ ${frequencyScale.max}Hz`);
      }
    }
    
    // 변환된 값(오른쪽 Y축) 범위 조정
    const convertedScale = scales['y-converted'] as any;
    if (convertedScale) {
      const allConvertedValues = chartDataRef.current.map(d => d.convertedValue);
      if (allConvertedValues.length > 0) {
        const minConverted = Math.min(...allConvertedValues);
        const maxConverted = Math.max(...allConvertedValues);
        const margin = Math.max(Math.abs(maxConverted - minConverted) * 0.1, 2); // 최소 2 마진
        
        convertedScale.min = Math.floor(minConverted - margin);
        convertedScale.max = Math.ceil(maxConverted + margin);
        console.log(`📊 듀얼차트 변환값축 범위: ${convertedScale.min} ~ ${convertedScale.max}`);
      }
    }
  }, []);

  // 🎯 차트 클리어
  const clearChart = useCallback(() => {
    if (!chartRef.current) return;

    chartDataRef.current = [];
    chartRef.current.data.labels = [];
    chartRef.current.data.datasets[0].data = [];
    chartRef.current.data.datasets[1].data = [];
    (chartRef.current.data.datasets[0] as any).pointBackgroundColor = [];
    (chartRef.current.data.datasets[1] as any).pointBackgroundColor = [];
    
    // 🎯 X축 및 Y축 범위 초기화 - 올바른 기본값으로 설정
    if (chartRef.current.options.scales) {
      const xScale = chartRef.current.options.scales.x as any;
      const frequencyScale = chartRef.current.options.scales['y-frequency'] as any;
      const convertedScale = chartRef.current.options.scales['y-converted'] as any;
      
      if (xScale) {
        xScale.min = -0.2; // 0.2초 마진으로 시작
        xScale.max = 5; // 기본 5초 범위
      }
      if (frequencyScale) {
        frequencyScale.min = 100;
        frequencyScale.max = 300;
      }
      if (convertedScale) {
        convertedScale.min = -10;
        convertedScale.max = 15;
      }
      console.log('🎯 듀얼차트 X축 및 Y축 범위 기본값으로 초기화');
    }
    
    chartRef.current.update();
    console.log('🧹 듀얼 Y축 차트 클리어');
  }, []);

  // 🎯 Y축 단위 변경 시 재계산
  const updateAxisUnit = useCallback(() => {
    if (!chartRef.current) return;

    // 기존 데이터를 새로운 단위로 재계산
    chartDataRef.current = chartDataRef.current.map(data => ({
      ...data,
      convertedValue: convertFrequencyToUnit(data.frequency)
    }));

    // 차트 데이터 업데이트 - 주파수와 변환값 모두 업데이트
    chartRef.current.data.datasets[0].data = chartDataRef.current.map(data => ({ x: data.time, y: data.frequency }));
    chartRef.current.data.datasets[1].data = chartDataRef.current.map(data => ({ x: data.time, y: data.convertedValue }));
    chartRef.current.data.datasets[1].label = yAxisUnit === 'semitone' ? '세미톤 (st)' : '큐톤 (Q)';
    
    // Y축 제목 업데이트 및 범위 재계산
    if (chartRef.current.options.scales && chartRef.current.options.scales['y-converted']) {
      const convertedScale = chartRef.current.options.scales['y-converted'] as any;
      convertedScale.title.text = yAxisUnit === 'semitone' ? '세미톤 (st)' : '큐톤 (Q)';
      
      // 🎯 기존 데이터에 맞게 Y축 범위 재계산
      if (chartDataRef.current.length > 0) {
        const allConvertedValues = chartDataRef.current.map(d => d.convertedValue);
        const minConverted = Math.min(...allConvertedValues);
        const maxConverted = Math.max(...allConvertedValues);
        const margin = Math.abs(maxConverted - minConverted) * 0.1 || 2;
        
        convertedScale.min = Math.floor(minConverted - margin);
        convertedScale.max = Math.ceil(maxConverted + margin);
        console.log(`🎯 단위 변경 후 변환값축 범위: ${convertedScale.min} ~ ${convertedScale.max}`);
      } else {
        // 데이터가 없으면 범위 제거 (자동 설정)
        delete convertedScale.min;
        delete convertedScale.max;
      }
    }

    // 차트 제목 업데이트
    if (chartRef.current.options.plugins?.title) {
      chartRef.current.options.plugins.title.text = `듀얼 Y축 비교 차트 - 주파수 vs ${yAxisUnit === 'semitone' ? '세미톤' : '큐톤'}`;
    }

    chartRef.current.update();
    console.log(`🔄 듀얼축 차트 단위 변경: ${yAxisUnit}`);
  }, [convertFrequencyToUnit, yAxisUnit]);

  // 🎯 초기화 및 Y축 단위 변경 감지
  useEffect(() => {
    initChart();
    
    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, [initChart]);

  useEffect(() => {
    updateAxisUnit();
  }, [updateAxisUnit, yAxisUnit]);

  // 🎯 음절 annotation 추가 (실제 구현)
  const addSyllableAnnotations = useCallback((syllableData: any[]) => {
    if (!chartRef.current || !syllableData || syllableData.length === 0) {
      console.log('🎯 듀얼축 차트: annotation 데이터 없음');
      return;
    }

    console.log('🎯 듀얼축 차트: 음절 데이터 상태 확인:', syllableData.map(s => ({
      syllable: s.syllable || s.text,
      time: s.time,
      start: s.start,
      end: s.end,
      hasActualRange: !!(s.start && s.end)
    })));

    const annotations: any = {};
    
    syllableData.forEach((syllable, index) => {
      // ✅ 실제 시간 범위 우선 사용 (백엔드에서 제공하는 정확한 범위)
      let syllableStart: number;
      let syllableEnd: number;
      
      if (syllable.start !== undefined && syllable.end !== undefined) {
        // 백엔드에서 제공하는 정확한 시간 범위 사용
        syllableStart = syllable.start;
        syllableEnd = syllable.end;
        console.log(`🎯 음절 ${index+1} "${syllable.syllable || syllable.text}": 정확한 범위 [${syllableStart.toFixed(3)}s ~ ${syllableEnd.toFixed(3)}s]`);
      } else {
        // 구버전 호환성을 위한 임시 범위 (더 이상 사용되지 않아야 함)
        const duration = 0.2; // ±0.1초
        syllableStart = syllable.time - (duration / 2);
        syllableEnd = syllable.time + (duration / 2);
        console.warn(`⚠️ 음절 ${index+1} "${syllable.syllable || syllable.text}": 임시 범위 사용 [${syllableStart.toFixed(3)}s ~ ${syllableEnd.toFixed(3)}s]`);
      }
      
      // 음절 구간 경계선 (시작)
      annotations[`syllable-start-${index}`] = {
        type: 'line',
        scaleID: 'x',
        value: syllableStart,
        borderColor: 'rgba(255, 99, 132, 0.8)',
        borderWidth: 2,
        borderDash: [5, 5],
        label: {
          display: false
        }
      };
      
      // 음절 구간 경계선 (끝)
      annotations[`syllable-end-${index}`] = {
        type: 'line',
        scaleID: 'x', 
        value: syllableEnd,
        borderColor: 'rgba(255, 99, 132, 0.8)',
        borderWidth: 2,
        borderDash: [5, 5],
        label: {
          display: false
        }
      };
      
      // 음절 라벨 박스 (고정 위치로 표시)
      const chartY = 400; // 고정 주파수 위치
      annotations[`syllable-label-${index}`] = {
        type: 'label',
        xValue: (syllableStart + syllableEnd) / 2,
        yValue: chartY, // 고정 위치에 표시
        backgroundColor: 'rgba(138, 43, 226, 0.9)',
        borderColor: 'rgba(138, 43, 226, 1)',
        borderRadius: 4,
        borderWidth: 1,
        color: 'white',
        content: syllable.label || syllable.syllable || `음절${index + 1}`,
        font: {
          size: 14,
          weight: 'bold'
        },
        padding: {
          x: 8,
          y: 6
        }
      };
      
      console.log(`🎯 음절 ${index + 1}: '${syllable.label || syllable.syllable}' x=${((syllableStart + syllableEnd) / 2).toFixed(3)}s y=${chartY}Hz`);
    });

    // Chart.js annotation 플러그인에 annotation 추가 (강제 초기화)
    if (!chartRef.current.options.plugins) {
      chartRef.current.options.plugins = {};
    }
    if (!chartRef.current.options.plugins.annotation) {
      chartRef.current.options.plugins.annotation = { annotations: {} };
    }
    
    chartRef.current.options.plugins.annotation.annotations = annotations;
    chartRef.current.update('resize'); // 강제 업데이트
    console.log(`🎯 듀얼축 차트: ${syllableData.length}개 음절 annotation 추가 완료`);
    console.log(`🎯 생성된 annotation:`, Object.keys(annotations));
  }, []);

  // 🎯 재생 진행 상황 업데이트 - 실제 구현
  const updatePlaybackProgress = useCallback((currentTime: number) => {
    if (!chartRef.current) return;

    const annotations = chartRef.current.options.plugins?.annotation?.annotations as any;
    if (!annotations) return;

    // 기존 재생 진행선 제거
    if (annotations['playback-progress']) {
      delete annotations['playback-progress'];
    }

    // 새로운 재생 진행선 추가
    annotations['playback-progress'] = {
      type: 'line',
      scaleID: 'x',
      value: currentTime,
      borderColor: 'rgba(255, 0, 0, 0.8)',
      borderWidth: 3,
      borderDash: [3, 3],
      label: {
        display: true,
        content: `${currentTime.toFixed(1)}s`,
        position: 'end',
        backgroundColor: 'rgba(255, 0, 0, 0.8)',
        color: 'white',
        padding: {
          x: 4,
          y: 2
        },
        font: {
          size: 10,
          weight: 'bold'
        }
      }
    };

    chartRef.current.update('none'); // 애니메이션 없이 즉시 업데이트
    console.log(`🎵 듀얼축 차트: 재생 진행 ${currentTime.toFixed(2)}s`);
  }, []);

  // 🎯 재생 진행 상황 클리어 - 실제 구현
  const clearPlaybackProgress = useCallback(() => {
    if (!chartRef.current) return;

    const annotations = chartRef.current.options.plugins?.annotation?.annotations as any;
    if (annotations && annotations['playback-progress']) {
      delete annotations['playback-progress'];
      chartRef.current.update('none');
      console.log('🎵 듀얼축 차트: 재생 진행 클리어');
    }
  }, []);

  // 🎯 피치 조정 (더미 구현)
  const adjustPitch = useCallback((direction: 'up' | 'down') => {
    console.log('🎵 듀얼축 차트: 피치 조정', direction);
    // TODO: 실제 피치 조정이 필요하면 여기에 추가
  }, []);


  // 🎯 전체 보기 (더미 구현)
  const resetView = useCallback(() => {
    console.log('🏠 듀얼축 차트: 전체 보기');
    // TODO: 실제 전체 보기 기능이 필요하면 여기에 추가
  }, []);

  // 🎯 실시간 피치 데이터를 가로선으로 표시하는 함수
  const addRealtimePitch = useCallback((frequency: number) => {
    if (!chartRef.current) return;

    const convertedValue = convertFrequencyToUnit(frequency);
    const chart = chartRef.current;
    
    console.log(`🎤 실시간 피치 데이터: ${frequency.toFixed(1)}Hz → ${convertedValue.toFixed(2)} ${yAxisUnit} (공식 검증용)`);

    // 🎯 annotation을 사용해서 가로선(수평선) 표시
    if (chart.options.plugins?.annotation?.annotations) {
      const annotations = chart.options.plugins.annotation.annotations as any;
      
      // Hz 값 가로선 (왼쪽 Y축)
      annotations.realtimeHz = {
        type: 'line',
        yMin: frequency,
        yMax: frequency,
        yScaleID: 'y-frequency',
        borderColor: 'rgba(255, 99, 132, 0.8)',
        borderWidth: 3,
        borderDash: [5, 5],
        label: {
          enabled: true,
          content: `${frequency.toFixed(1)}Hz`,
          position: 'start',
          backgroundColor: 'rgba(255, 99, 132, 0.9)',
          color: 'white',
          font: {
            size: 12,
            weight: 'bold'
          }
        }
      };
      
      // 변환된 값 가로선 (오른쪽 Y축)
      annotations.realtimeConverted = {
        type: 'line',
        yMin: convertedValue,
        yMax: convertedValue,
        yScaleID: 'y-converted',
        borderColor: 'rgba(54, 162, 235, 0.8)',
        borderWidth: 3,
        borderDash: [5, 5],
        label: {
          enabled: true,
          content: `${convertedValue.toFixed(2)}${yAxisUnit === 'semitone' ? 'st' : 'Q'}`,
          position: 'end',
          backgroundColor: 'rgba(54, 162, 235, 0.9)',
          color: 'white',
          font: {
            size: 12,
            weight: 'bold'
          }
        }
      };
      
      chart.update('none');
    }
  }, [convertFrequencyToUnit, yAxisUnit]);

  // 🎯 실시간 가로선 제거 함수
  const clearRealtimePitch = useCallback(() => {
    if (!chartRef.current) return;

    const chart = chartRef.current;
    if (chart.options.plugins?.annotation?.annotations) {
      const annotations = chart.options.plugins.annotation.annotations as any;
      delete annotations.realtimeHz;
      delete annotations.realtimeConverted;
      chart.update('none');
    }
  }, []);

  return {
    addDualAxisData,
    clearChart,
    updateYAxisRanges,
    chartData: chartDataRef.current,
    setYAxisUnit,
    yAxisUnit,
    // 🎯 새로 추가된 실시간 피치 검증 함수들
    addRealtimePitch,
    clearRealtimePitch,
    // 기존 함수들
    addSyllableAnnotations,
    updatePlaybackProgress,
    clearPlaybackProgress,
    adjustPitch,
    resetView
  };
};